.publications .container {
  @media (max-width: 540px) {
    padding: 0;
  }
}

.publications__title {
  font-size: 64px;
  font-weight: 400;
  width: 100%;

  @media (max-width: 540px) {
    font-size: 36px;
    margin: 40px 0 0 0;
    padding: 0 0 0 20px;
  }
}

.puslications__list {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  @media (max-width: 540px) {
    margin: 20px 0 0 0;
  }
}

.publications__item {
  position: relative;
  width: 30%;
  display: grid;
  grid-template-columns: 75% 25%;
  grid-template-rows: 310px 30px 30px minmax(70px, max-content);
  row-gap: 20px;
  grid-template-areas: "D D" "C C" "A A" "B E";
  background-color: $publ-color;
  margin: 25px 25px;
  padding: 20px;
  transition: $transition-fast;

  &:hover {
    background-color: $sky;
    transition: $transition-fast;

    .publications__item-img img {
      height: 105%;
      width: 105%;
      transition: $transition-slow;
    }
  }

  @media (max-width: 1539px) {
    width: 449px;
  }

  @media (max-width: 1037px) and (min-width: 541px) {
    width: 100%;
    grid-template-columns: 310px 1fr;
    grid-template-rows: minmax(30px, max-content) minmax(70px, max-content) minmax(30px, max-content) 30px;
    row-gap: 20px;
    column-gap: 30px;
    grid-template-areas: "D A" "D B" "D C" "D E";

    &:hover .publications__item-img img {
      height: 100%;
      width: 100%;
    }
  }

  @media (max-width: 790px) {
    width: 380px;
    grid-template-columns: 340px;
    grid-template-rows: 310px minmax(30px, max-content) minmax(70px, max-content) minmax(30px, max-content) 30px;
    grid-template-areas: "D" "A" "B" "C" "E";
  }

  @media (max-width: 540px) {
    width: 100%;
    grid-template-columns: 100%;
    grid-template-rows: min-content minmax(30px, max-content) minmax(30px, max-content) 0 30px;
    column-gap: 0;
    padding: 0 0 20px 0;
    border-radius: 0;
    box-shadow: none;
    margin: 0;

    &:hover .publications__item-img img {
      height: auto;
      width: 100%;
      background-color: $publ-color;
    }

    &:hover {
      background-color: $publ-color;
    }
  }
}

.publications__item-title {
  grid-area: A;
  font-weight: 400;
  margin: 0;

  @media (max-width: 540px) {
    text-align: center;
  }
}

.publications__item-img {
  grid-area: D;
  overflow: hidden;
  transition: $transition-slow;

  img {
    height: 100%;
    width: 100%;
    max-width: none;
    max-height: none;
    transition: $transition-slow;
  }

  @media (max-width: 540px) {
    border-radius: 0;

    img {
      width: 100%;
      height: auto;
    }
  }
}

.publications__item-date {
  grid-area: C;
  margin: 0;
  font-size: 18px;
  color: $publ-date;

  @media (max-width: 540px) {
    display: none;
  }
}

.publications__item-desc {
  grid-area: B;
  margin: 0;

  @media (max-width: 540px) {
    padding: 0 50px;
    text-align: center;
  }
}

.publications__item-link {
  grid-area: E;
  position: absolute;
  bottom: 5px;
  left: 5px;
  font-size: 20px;
  transition: $transition-slow;

  &:hover {
    font-size: 22px;
    transition: $transition-slow;
    color: $white;
  }

  @media (max-width: 790px) {
    position: static;
    text-align: center;
  }

  @media (max-width: 540px) {
    &:hover {
      color: $black;
    }
  }
}
